<template>
    <div class="containner">
      <div class="head">
        <p>酷学院</p>
        <div class="right-tip">
          <el-button type="primary" size="medium">下载APP</el-button>
          <el-dropdown>
            <span class="el-dropdown-link">登录<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
      <div class="main">
        <el-row>
          <el-col :span="12" :offset="3">
            <div class="form">
              <p class="header">欢迎您加入酷学院</p>
              <form action="">
                <p class="form-header">个人登记表单</p>
                <div class="singleText" v-for="(item, index) in inputList" :key="index" @click="singleTextFun(type)">
                  <p class="title">{{singleData.singleTitle}}</p>
                  <p class="description">{{singleData.singleDescripe}}</p>
                  <el-input placeholder="" v-model="singleInput"></el-input>
                </div>
                <div class="singleChoice" v-for="(item, index) in radioList" :key="index" @click="ChoiceFun(type)">
                  <p class="title">{{radioTitle}}</p>
                  <p class="description">{{radioText}}</p>
                  <el-radio-group v-model="radio">
                    <el-radio :label="1">{{one}}</el-radio>
                    <el-radio :label="2">{{two}}</el-radio>
                    <el-radio :label="3">{{three}}</el-radio>
                    <el-radio :label="4">{{four}}</el-radio>
                  </el-radio-group>
                </div>
                <div class="dropDownBox" v-for="(item, index) in selectList" :key="index" @click="dropDownFun(type)">
                  <p class="title">{{selectData.selectTitle}}</p>
                  <el-select v-model="options.value" placeholder="请选择">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </div>
                <div class="rectangularRadio" v-for="(item, index) in tableList" :key="index" @click="rectangularFun(type)">
                  <p class="title">{{rectangular}}</p>
                  <el-table
                    :data="tableData"
                    border>
                    <el-table-column
                      prop="date"
                      label=""
                      width="180"
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="name"
                      :label="tableData1[0].name"
                      width="180"
                      align="center">
                      <template slot-scope="">
                        <el-radio v-model="radio1"></el-radio>
                      </template>
                    </el-table-column>
                    <el-table-column
                      prop="address"
                      :label="tableData1[1].name"
                      align="center">
                      <template slot-scope="">
                        <el-radio v-model="radio2"></el-radio>
                      </template>
                    </el-table-column>
                    <el-table-column
                      prop="address"
                      :label="tableData1[2].name"
                      align="center">
                      <template slot-scope="">
                        <el-radio v-model="radio3"></el-radio>
                      </template>
                    </el-table-column>
                    <el-table-column
                      prop="address"
                      :label="tableData1[3].name"
                      align="center">
                      <template slot-scope="">
                        <el-radio v-model="radio4"></el-radio>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
              </form>
              <el-button type="primary" class="submit">提交</el-button>
            </div>
          </el-col>
          <el-col :span="6" :offset="3">
            <div class="operating">
              <div class="operating-item">
                <p class="operating-tit">添加字段</p>
                <div class="choose">
                  <el-button size="medium" @click="addSingleText(1)">单行文本</el-button>
                  <el-button size="medium" @click="addSingleText(4)">矩形单选</el-button>
                </div>
                <div class="choose">
                  <el-button size="medium" @click="addSingleText(2)">单项选择</el-button>
                  <el-button size="medium" @click="addSingleText(3)">下拉框</el-button>
                </div>
              </div>
              <div class="operating-item" v-if="type===1">
                <p class="operating-tit">单行文本</p>
                <div class="motify">
                  <p class="title">标题</p>
                  <el-input placeholder="" v-model="singleData.singleTitle"></el-input>
                </div>
                <div class="motify">
                  <p class="title">标题描述</p>
                  <el-input placeholder="" v-model="singleData.singleDescripe"></el-input>
                </div>
              </div>
              <div class="operating-item" v-if="type===2">
                <p class="operating-tit">单项选择</p>
                <div class="motify">
                  <p class="title">标题</p>
                  <el-input placeholder="" v-model="radioTitle"></el-input>
                </div>
                <div class="motify">
                  <p class="title">标题描述</p>
                  <el-input placeholder="" v-model="radioText"></el-input>
                </div>
                <div class="motify">
                  <p class="title">选项</p>
                  <el-input placeholder="" v-model="one"></el-input>
                  <el-input placeholder="" v-model="two"></el-input>
                  <el-input placeholder="" v-model="three"></el-input>
                  <el-input placeholder="" v-model="four"></el-input>
                </div>
              </div>
              <div class="operating-item" v-if="type===3">
                <p class="operating-tit">下拉框</p>
                <div class="motify">
                  <p class="title">标题</p>
                  <el-input placeholder="" v-model="selectData.selectTitle"></el-input>
                </div>
              </div>
              <div class="operating-item" v-if="type===4">
                <p class="operating-tit">矩形单选</p>
                <div class="motify">
                  <p class="title">标题</p>
                  <el-input placeholder="" v-model="rectangular"></el-input>
                </div>
                <div class="motify">
                  <p class="title">列选项</p>
                  <el-input placeholder="" v-model="tableData[index].date" v-for="(item,index) in tableData" :key=index></el-input>
                </div>
                <div class="motify">
                  <p class="title">行选项</p>
                  <el-input placeholder="" v-model="tableData1[index].name" v-for="(item,index) in tableData1" :key=index></el-input>
                </div>
              </div>
              <div class="operating-item">
                <p class="operating-tit">提交</p>
                <div class="motify">
                  姓名：<p>{{singleInput}}</p>
                  学历：<p v-if="radio===1">高中</p>
                  <p v-if="radio===2">大学</p>
                  <p v-if="radio===3">硕士</p>
                  <p v-if="radio===4">博士</p>
                  地址：
                  <p>{{options.value}}</p>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
</template>
<script>
export default {
  name: 'index',
  data () {
    return {
      radioTitle: '你的最高学历',
      radioText: '告诉我们你的最高学历，我们只是想了解下',
      inputList: 1,
      radioList: 1,
      selectList: 1,
      tableList: 1,
      radio: 1,
      radio1: 1,
      radio2: 1,
      radio3: 1,
      radio4: 1,
      one: '高中',
      two: '大学',
      three: '硕士',
      four: '博士',
      options: [{
        value: '北京',
        label: '北京'
      }, {
        value: '上海',
        label: '上海'
      }],
      whereValue: '', // 地址
      tableData: [{
        date: '沟通技巧'
      }, {
        date: '时间观念'
      }, {
        date: '技能熟练'
      }],
      tableData1: [{
        name: '较差一些'
      },
      {
        name: '一般了'
      },
      {
        name: '还不错'
      },
      {
        name: '这是我的强项'
      }
      ],
      type: 1,
      singleData: {
        singleTitle: '留下你的大名吧',
        singleDescripe: '请告诉我们你的大名或绰号都行'
      },
      selectData: {
        selectTitle: '你来自哪里呢'
      },
      singleInput: '',
      rectangular: '自我评估测试'
    }
  },
  methods: {
    addSingleText (val) {
      if (val === 1) {
        this.inputList += 1
      } else if (val === 2) {
        this.radioList += 1
      } else if (val === 3) {
        this.selectList += 1
      } else if (val === 4) {
        this.tableList += 1
      }
    },
    singleTextFun (type) {
      this.type = 1
    },
    ChoiceFun (type) {
      this.type = 2
    },
    dropDownFun (type) {
      this.type = 3
    },
    rectangularFun (type) {
      this.type = 4
    }
  }
}
</script>
<style lang="less">
.containner{
  background-color: #F6FCFC;
  height:100vh;
  p{
    padding:0;
    margin:0;
  }
  .head{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #fff;
    box-sizing: border-box;
    border-bottom: 1px #ccc solid;
    >p{
      color:orange;
    }
    .right-tip{
      button{
        margin-right:10px;
      }
      .el-dropdown{
        span{
          color:#438AFE;
        }
      }
    }
  }
  .main{
    padding:10px 0;
    .form{
      background-color: #fff;
      border-radius: 10px;
      box-sizing: border-box;
      box-shadow: 1px 2px 1px 3px #c2c1c1;
      .header{
        text-align: left;
        padding:10px;
        background-color: #438AFE;
        color:#fff;
        border-radius: 10px 10px 0px 0;
      }
      form{
        .form-header{
          color:#3f3f3f;
          padding:10px;
          text-align: left;
          font-weight: bold;
          border-bottom:1px #ccc solid;
        }
        .title{
            text-align: left;
            padding:10px;
            font-weight: bold;
          }
          .description{
            text-align: left;
            padding:10px;
          }
          .el-input{
            margin:10px;
            box-sizing: border-box;
          }
        .singleChoice{
          background-color: #DEEAFF;
        }
      }
      .submit{
        margin:10px;
        width: 150px;
      }
    }
    .operating{
      .operating-item{
        .operating-tit{
          background-color: #438AFE;
          color:#fff;
          padding:10px;
        }
        .choose{
          display: flex;
          justify-content: space-between;
          margin:5px 10px;
          button{
            width:110px;
            margin:5px 0;
          }
        }
        .motify{
          margin:0 10px;
          p{
            padding:6px 0;
          }
          .el-input{
            margin: 5px 0;
          }
        }
      }
    }
  }
}
</style>



